      <li>
        Collate all competitor brand names and input into Keyword Planner (or
        similar tool) to get average monthly search volume.
      </li>
      <li>
        The brand name with the highest average monthly search volume is the
        number 1 player in the category.
      </li>
      <li>
        This brand will then become the benchmark against which all other
        competitors will be compared against.
      </li>
      <li>
        However, depending on the 'People also search for results' it may be
        necessary to filter this list to avoid skewing the data or to make it
        more relevant for the chosen brand.
