<template>
  <b-container class="main-container">
    <h1>Share of Search</h1>
    <b-row>
      <b-col cols="6">
        <b-form-file
          v-model="selectedFile"
          :state="Boolean(selectedFile)"
          placeholder="Choose a export or drop it here..."
          drop-placeholder="Drop file here..."
        ></b-form-file>
      </b-col>
      <b-col cols="6" class="d-flex flex-row justify-space-around">
        <b-button
          variant="outline-primary"
          v-b-modal.modal-1
          style="flex-grow: 1"
          >Instructions</b-button
        >
        <Instructions />
        <b-button
          variant="outline-success"
          @click="export_png"
          class="ml-3"
          style="flex-grow: 1"
          >Export to PNG</b-button
        ><b-button
          variant="outline-success"
          @click="export_csv"
          class="ml-3"
          style="flex-grow: 1"
          >Export to CSV</b-button
        ></b-col
      >
    </b-row>
    <b-alert class="mt-3" :show="alert.show" dismissible fade>
      {{ alert.message }}
    </b-alert>
    <div class="chart-container ">
      <vue-resizable
        :fitParent="true"
        :active="['rt', 'r', 'rb', 'b']"
        class="resize-container"
      >
        <canvas id="myChart" width="800" height="500"></canvas>
      </vue-resizable>
    </div>
  </b-container>
</template>

<script>
import DataFrame from 'dataframe-js'
import Chart from 'chart.js'
import 'chartjs-plugin-colorschemes'
import VueResizable from 'vue-resizable'
import Instructions from '@/components/Instructions.vue'

export default {
  name: 'MainComponent',
  components: { VueResizable, Instructions },
  data() {
    return {
      selectedFile: undefined,
      processedDataFrame: undefined,
      chart: undefined,
      alert: {
        show: false,
        message: undefined
      }
    }
  },
  watch: {
    selectedFile(new_val) {
      if (new_val) {
        this.processFile(new_val)
      }
    }
  },
  methods: {
    processFile(file) {
      file.text().then(t => {
        let csv_section = t
          .split('\n')
          .slice(2)
          .join('\n')
        DataFrame.fromCSV(new File([csv_section], 'input.csv')).then(df => {
          df = this.processDataFrame(df)
          this.processedDataFrame = df
          this.updateChart(df)
        })
      })
    },
    processDataFrame(df) {
      let value_columns = df.listColumns().filter(c => c != 'Month')

      if (value_columns.length > 10) {
        this.showAlert(
          'Only a maximum of 10 columns can be plotted. Choosing first 10 columns.'
        )
        value_columns = value_columns.slice(0, 10)
        df = df.select('Month', ...value_columns)
      }

      df = df.map(r => {
        value_columns.forEach(c => {
          r = r.set(c, r.get(c) == '<1' ? 0 : +r.get(c))
        })
        return r
      })

      let vals = df.transpose().toArray()
      let processed = []

      for (let i = 0; i < vals[0].length - 11; i++) {
        processed.push(
          vals.map(v => {
            if (typeof v[0] == 'string') {
              return v[i + 11]
            } else {
              let sum = v.slice(i, i + 12).reduce((a, b) => a + b, 0)
              return sum / 12
            }
          })
        )
      }

      let new_df = new DataFrame(processed, df.listColumns())
      new_df = new_df.map(r => {
        let row_sum = value_columns
          .map(c => r.get(c))
          .reduce((a, b) => a + b, 0)
        value_columns.forEach(c => {
          r = r.set(c, r.get(c) / row_sum)
        })
        return r
      })

      return new_df
    },
    updateChart(df) {
      const value_columns = df.listColumns().filter(c => c != 'Month')
      value_columns.sort((a, b) => df.stat.mean(b) - df.stat.mean(a))

      let data = {
        labels: df
          .select('Month')
          .transpose()
          .toArray()[0],
        datasets: value_columns.map((c, i) => ({
          data: df
            .select(c)
            .transpose()
            .toArray()[0],
          label: c,
          fill: i == 0 ? 'origin' : '-1',
          pointRadius: 0
        }))
      }
      if (!this.chart) {
        let options = {
          tooltips: {
            enabled: false
          },
          responsive: true,
          layout: {
            padding: {
              left: 20,
              right: 20,
              top: 20,
              bottom: 20
            }
          },
          maintainAspectRatio: false,
          spanGaps: false,
          elements: {
            line: {
              tension: 0.000001
            }
          },
          scales: {
            yAxes: [
              {
                stacked: true,
                ticks: {
                  min: 0,
                  max: 1,
                  callback: function(value) {
                    return Math.round(value * 100) + '%'
                  }
                }
              }
            ]
          },
          plugins: {
            filler: {
              propagate: false
            },
            'samples-filler-analyser': {
              target: 'chart-analyser'
            },
            colorschemes: {
              scheme: 'tableau.Tableau10'
            }
          }
        }

        var ctx = document.getElementById('myChart')
        this.chart = new Chart(ctx, {
          type: 'line',
          data: data,
          options: options
        })
      } else {
        this.chart.data = data
        this.chart.update()
      }
    },
    export_png() {
      let canvas = document.getElementById('myChart')
      let dataURL = canvas.toDataURL()
      let element = document.createElement('a')
      element.setAttribute('href', dataURL)
      element.setAttribute('download', 'chart.png')
      document.body.appendChild(element)
      element.click()
      document.body.removeChild(element)
    },
    export_csv() {
      let csv_text = this.processedDataFrame.toCSV(true)
      let element = document.createElement('a')
      element.setAttribute(
        'href',
        'data:text/plain;charset=utf-8, ' + encodeURIComponent(csv_text)
      )
      element.setAttribute('download', 'chart_data.csv')
      document.body.appendChild(element)
      element.click()
      document.body.removeChild(element)
    },
    showAlert(message) {
      this.alert.message = message
      this.alert.show = true
      setTimeout(() => {
        this.alert.show = false
      }, 5000)
    }
  }
}
</script>

<style>
html {
  min-width: 800px;
}

.main-container {
  height: 100%;
  margin-top: 15px;
}
.chart-container {
  height: 80%;
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.resize-container {
  border: 1px solid lightgray;
  border-radius: 8px;
}
</style>
